import React, { useState, useEffect, useContext } from 'react';
import { Row, Col, ListGroup, Image, Card, Container, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { StoreContext } from '../context/StoreContext';
import { CartContext } from '../context/CartContext';
import Message from '../components/Message';
import axios from 'axios';
import { Loader } from '../components/Loader';
import { PayPalButton } from 'react-paypal-button-v2';

export const OrderPage = ({ match, history }) => {
   const orderId = match.params.id;
   const [order, setOrder] = useState({});
   const [loading, setLoading] = useState(true);
   const [errorMsg, setErrorMsg] = useState('');
   const { removeCart } = useContext(CartContext);
   const { existUser } = useContext(StoreContext);

   let config = {
      headers: {
         'Authorization': `Bearer ${existUser.token}`,
         'Content-Type': 'application/json',
      },
   };

   useEffect(() => {
      async function fetchOrderById() {
         try {
            const { data: foundOrder } = await axios.get(`/api/orders/${orderId}`, config);
            setOrder(foundOrder);
            setLoading(false);
            const { data: keyData } = await axios.get('/api/config/paypal/key', config);
            if (keyData) {
               const script = document.createElement('script');
               script.type = 'text/javascript';
               script.src = `https://www.paypal.com/sdk/js?client-id=${keyData.key}`;
               document.body.appendChild(script);
            }
         } catch (error) {
            const message =
               error.response && error.response.data.message
                  ? error.response.data.message
                  : error.message;
            setErrorMsg(message);
         }
      }
      fetchOrderById();
      // eslint-disable-next-line
   }, []);

   const createOrderHandler = (details, actions) => {
      return actions.order.create({
         purchase_units: [
            {
               amount: {
                  currency_code: 'USD',
                  value: order.totalPrice,
               },
            },
         ],
      });
   };

   const successPaymentHandler = async (details, data) => {
      setLoading(true);
      const { intent, status, update_time, create_time } = details;
      if (intent) {
         if (status === 'COMPLETED') {
            const dataToUpdate = {
               orderId: order._id,
               isPaid: true,
               paidAt: create_time,
               isDelivered: true,
               deliveredAt: update_time,
            };
            try {
               const { data: updatedOrder } = await axios.patch(
                  `/api/orders/`,
                  { ...dataToUpdate },
                  config
               );
               setOrder(updatedOrder);
               // clean the cart
               removeCart();
               //    history.push('/profile');
               setLoading(false);
            } catch (error) {
               const message =
                  error.response && error.response.data.message
                     ? error.response.data.message
                     : error.message;
               setErrorMsg(message);
            }
         }
      }
   };

   return (
      <>
         {loading === true ? (
            <Loader />
         ) : (
            <>
               <h3>Order # {orderId}</h3>
               <Row>
                  <Col md={8}>
                     <ListGroup variant='flush'>
                        <ListGroup.Item>
                           <h4>Shipping</h4>
                           <p>
                              <strong>Names: </strong>
                              {order.user.name}
                           </p>
                           <p>
                              <strong>Email: </strong> {order.user && order.user.email}
                           </p>
                           {order.shippingAddress && (
                              <p>
                                 <strong>Address:</strong>
                                 {order.shippingAddress.address}, {order.shippingAddress.city}{' '}
                                 {order.shippingAddress.postalCode}, {order.shippingAddress.country}
                              </p>
                           )}
                           {order.isDelivered ? (
                              <Message variant='success'>Delivered on {order.deliveredAt}</Message>
                           ) : (
                              <Message variant='danger'>Not Delivered</Message>
                           )}
                        </ListGroup.Item>
                        <ListGroup.Item>
                           <h4>Payment Method</h4>
                           <p>{order.paymentMethod}</p>
                           {order.isPaid ? (
                              <Message variant='success'>Paind on {order.paidAt}</Message>
                           ) : (
                              <Message variant='danger'>Not paid</Message>
                           )}
                        </ListGroup.Item>
                        <ListGroup.Item>
                           <h4>Order Items</h4>
                           {order.orderItems.length === 0 ? (
                              <Message>Order is empty</Message>
                           ) : (
                              <ListGroup variant='flus'>
                                 {order.orderItems.map((item, index) => (
                                    <ListGroup.Item key={index}>
                                       <Row>
                                          <Col md={1}>
                                             <Image
                                                src={item.image}
                                                alt={item.name}
                                                fluid
                                                rounded
                                             />
                                          </Col>
                                          <Col>
                                             <Link to={`/product/${item._id}`}>{item.name}</Link>
                                          </Col>
                                          <Col md={1}>
                                             {item.size}
                                             {'"'}
                                          </Col>
                                          <Col md={4}>
                                             {item.quantity} x ${item.price} = $
                                             {item.quantity * item.price}
                                          </Col>
                                       </Row>
                                    </ListGroup.Item>
                                 ))}
                              </ListGroup>
                           )}
                        </ListGroup.Item>
                     </ListGroup>
                  </Col>
                  <Col md={4}></Col>
               </Row>
               <Container>
                  <Row>
                     <Col md={3}>
                        {!order.isPaid ? (
                           <PayPalButton
                              amount={order.totalPrice}
                              currency={'USD'}
                              createOrder={createOrderHandler}
                              onSuccess={successPaymentHandler}
                              style={{
                                 color: 'blue',
                                 shape: 'pill',
                              }}
                           />
                        ) : (
                           <Button
                              variant='primary'
                              onClick={() => {
                                 history.push('/profile');
                              }}
                           >
                              My Purchase
                           </Button>
                        )}
                     </Col>
                  </Row>
               </Container>
            </>
         )}
      </>
   );
};
